import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function bottomLineProfitMarginTtm(tableName: string) {
  const title = "Bottom Line Margin (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["bottomLineProfitMarginTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Net income as a % of revenue.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about bottom line profit margin or other relevant financial measures?`,
              value: info.row.original.financialData.companyRatios?.data?.bottomLineProfitMarginTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            (info.row.original.financialData.companyRatios?.data?.bottomLineProfitMarginTtm ?? 0) *
            100
          }
        />
      )
    },
  })
}

export { bottomLineProfitMarginTtm }
